// v-on:blur="this.$parent.validated = true"
export default{
    template: `
    <section v-if="isVisible()" >
        <datalist :id="id">
            <option v-for="opt in info.opts" :value="opt"></option>
        </datalist>
        <div class="form-group" >
            <label>{{info.title}}</label>
            <input type="text" 
                v-bind:class="{'form-control':true, 'is-invalid': !isValid(val) && this.$parent.validated }"
                v-model="val" @change="reportVal" :list="id" :required='info.isRequired' 
                :placeHolder='info.placeHolder' />
            <div class="invalid-feedback">
                Please provide a valid data.
            </div>
        </div>
        
    </section>
    `,
    //props : {info :{ type: Object, required: true}},
    props: ['info'],
    data() {
        return {
            //id: this.info.name + "listId", 
            id: this.info.name + 'listId',
            val: '',
            visible:false
        }
    },
    methods: {
        checkIsVisible() {
            const dataSur = 'this.$parent.surveyAnswer["'
            let r = this.info.visibleIf.replace(/{ /g, dataSur).replace(/ }/g,'"]')  //replace all '{' to dataSur and '} to ']'
            return eval(r.trim())
        },
        isVisible() { 
            console.log(this.$parent.answer) //tdk boleh dicomment ato dihapus, perlu untuk ini agar trigger on running
           
            let lVisible = false
            if (this.info.visibleIf == null) {
                //console.log("visibleIfWithoutIf")
                lVisible = true
            } else {
                if(!this.isAnyNull()) {
                    if(this.checkIsVisible()){
                    //if(this.getAnswerByName("availability") < 3 ) {
                    //if(this.$parent.surveyAnswer["availability"] < 3 ) {
                        console.log("xxx1")
                        lVisible = true
                    }
                }
            }   
                        //console.log("isVisible End xxxxxxxxxxxxx", lVisible)
                        //alert("lVisible")
            if(!lVisible) { 
                this.val = '' 
            }
            return lVisible 
        },
        getAnswerByName(val) {
            const pref = "this.$parent.surveyAnswer['"
            const toEval = pref + val.trim() +"']"
            return eval(toEval) 
        },
        isAnyNull(){
            var i, param, str = this.info.visibleIf
            str.match(/\{.*?\}/g).map(x => x.replace(/[{}]/g, ""))  //get param
            param =str.match(/\{.*?\}/g).map(x => x.replace(/[{ }]/g, ""));
            for (i = 0, param.length; i < param.length; i++) { 
                if(this.getAnswerByName(param[i]) == null) {
                    return true
                }
            }
            return false 
        },
        reportVal() {
            this.$emit('setVal', this.val)
        },
        isValid: function(nilai) {
            console.log("isValid xxxx ", nilai)
            if(this.info.isRequired ){
                if(nilai.length < 20) {
                    return false
                }else {
                    return true
                }

            }else {
                return true
            }
            

            //console.log("isvalid")
            // //if(nilai) {console.log("nx", nilai)}
            //this.blured = true
            // this.$emit('validateMe', nilai)
            // console.log("parent valid",this.$parent.valid)
            //  console.log(nilai == undefined)
            //  console.log(nilai == null)
            //  console.log(nilai == "")
            // console.log("blured", this.blured)
            // if(nilai == undefined){
            //     console.log(123)
            // }
            //this.$emit('validateMe', nilai)
            //console.log("nilai", nilai)
            //this.$parent.isValid = ( nilai == "test"  || nilai =="rest")

            //return nilai == "test"  || nilai =="rest"
            //return this.$parent.isValid

            //val == "test"? true: false
            //console.log('valid child: ',this.valid)
            //return this.valid    
        }
    },
    computed:{
        listId(){
            return 'list' + this._uid
        }

    }
}
