// class="custom-select my-1 mr-sm-2" 

export default{
    template: `
    <section>
        <div class="form-group">
            <label class="my-1 mr-2" > {{info.label}} </label>
            <select 
                v-bind:class="{'form-control':true, 'is-invalid' : !isValid(val) && this.$parent.validated }"
                v-model="val" @change="reportVal" :required='info.isRequired' >
                <option selected>Choose...</option>
                <option v-for="opt in info.opts">{{opt}}</option>
            </select>
            <div class="invalid-feedback">
                Please select option available.
            </div>
        </div>
    </section>
    `,
    props: ['info'],
    data() {
        return {
            val: '',
        }
    },
    methods: {
        reportVal() {
            this.$emit('setVal', this.val)
        },
        isValid: function() {
            //console.log(nilai)
            //validate here
            return true
        }
    }
}
