                // </p>
export default{
    template: `
    <section>
        <div class="row form-group bg-light mb-1 mt-1">
            <div class="col-sm-6 mt-2 ">
                <h6><b>{{info.title}}</b></h6>
                
            </div>
            <div class="col-sm-6 mt-2 ">
                <ul 
                    v-bind:class="{'pagination justify-content-end':true, 'is-invalid': !isValid()  }" >
                    <li class="page-item" v-for="opt in info.opts" :key="opt" 
                        @click="itemActive(opt)"
                        :class="[(itemA == opt) ? 'active':'' ]"
                    >
                        
                        <a class="page-link" href="javascript:void(0);" v-on:click="reportVal(opt)"> {{opt}}</a>
                    </li>
                </ul>
                <div class="invalid-feedback text-right" style="padding-right:14px">Option is not selected. 
                </div>
                
            </div>
        </div>
    </section>
    `,
    props: ['info'],
    data() {
        return {
            val: '',
            itemA:'0'
        }
    },
    methods: {
        reportVal(nilai) {
            //console.log("reportVal", nilai)
            this.val = nilai
            this.$emit('setVal', this.val)
        },
        isValid(val){
            console.log("isvalid", this.valid, val)
            if(this.$parent.validated) {
                return false
            }else{return true}
        },
        itemActive(e) {
            this.itemA = e;
        }
    }
}
