export default{
    template: `
    <section>
        <div class="row form-group bg-light mb-1 mt-1">
            <div class="col-sm-8 mt-2">
                <h6><b>{{info.title}}</b></h6>
                <p class="mb-0"><small>
                  {{info.content_En}} <br/>
                  <a class="text-primary"><em> {{info.content_Id}} </em></a>
                  </small>
                </p>
            </div>
            <div class="col-sm-4 mt-2 ">
                <div v-bind:class="{'form-group d-flex justify-content-end':true, 'is-invalid': !isValid() }">
                    
                    <div v-for="n in info.max" class="custom-control custom-radio custom-control-inline"  >
                        <input type="radio"
                            v-bind:class="{'custom-control-input':true, 'is-invalid' : !checkValid(val) }"
                            v-model="val"  :value="n" @change="reportVal" :required="false" 
                            :name="optionName(n)" :id="optionName(n)" />

                        <label class="custom-control-label" :for="optionName(n)">{{ n }}</label>
                    </div>  
                    

                </div>
                <div class="invalid-feedback text-right" style="padding-right:14px">Option is not selected. 
                </div>
            </div>
          

        </div>
    </section>
    `,
    props: ['info'],
    data() {
        return {
            val: '',
            valid: true,
        }
    },
    methods: {
        optionName(n){
            return this.info.title.trim().replace(" ","") + n
        },
        reportVal() {
            this.$emit('setVal', this.val)
        },
        isValid(val){
            console.log("isvalid", this.valid, val)
            if(this.$parent.validated) {
                return true
            }else{
                return true
            }
        },
        checkValid(val){
            //console.log(val)
            console.log("checkValid", this.valid, val)
            //this.valid=false
            return this.valid
        }

    },
    computed:{
        listId(){
            return 'list' + this._uid
        },
        
        // checkValid(val){
        //     console.log(val)
        //     //this.valid=false
        //     return this.valid
        // }
    }
}
